.btn.focus, .btn:focus {
    outline: 0 !important;
    box-shadow: none !important;
}
.site_btn {
    & .btnRadius{
        background: var(--dark-gray2);
        padding: .7rem 3rem;
        border-radius: 2.4rem;
        font-size: 1.4rem;
        color: var(--white-color);
        font-weight: 500;        
        border:0;
    }
    & .btnBorder{
        border: .1rem solid #efefef;
        border-radius: .4rem;
        color: var(--light-gray);
        font-weight: 700;
        font-size: 1.5rem;
        padding: 1.1rem 3rem;
    }
}
.details_right{
    & a{
        display:block;
    }
}