:root{
   --sidebar-bg-color:#fefefe;
   --center-color:#f5f5fc;
   --site-font-family:'Open Sans', sans-serif;
   --site-font:14px;
   --light-gray:#b9b9b9;
   --dark-gray:#707070;
   --dark-gray2:#b5b5b5;
   --theme-color:#4ac1f0;
   --green-color:#40bb79;
   --red-color:#fb2626;
   --white-color: #ffffff;
   --border-color:#efefef; 
   --fb-color:#3c5898;
   --crl-width: 23rem;
   --crl-left:0rem;
   
   --crr-width: 0rem; //40rem
   --crr-right:40rem;
    
}

html{
    font-size: 10px;
}
body{
    box-sizing: border-box;
    font-family: var(--site-font-family);
    font-size: var(--site-font);
    background: var(--white-color);
}
img{
    max-width: 100%;
}