.contactUs {
    background: var(--white-color);
    padding: 5rem 4rem;
    margin-top: 5rem;
    border-radius: 2rem;
    @media screen and (max-width: 767px) {
        padding: 0rem 1rem;
    }
    & li {
        & + li {
            border-top: 0.12rem solid var(--border-color);
            margin-top: 2rem;
            padding-top: 2.5rem;
        }
    }
    & h2 {
        font-size: 1.5rem;
        color: var(--dark-gray-color);
        font-weight: 700;
        position: relative;
        padding-left: 4rem;
        margin: 0 0 2rem;
        &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 3rem;
            height: 3rem;
        }
        &.call_icon:before {
            background: url(../../assets/callIcon.png) no-repeat left top / 2.5rem;
        }
        &.mail_icon:before {
            background: url(../../assets/mailIcon.png) no-repeat left top / 2.5rem;
        }
        &.location_icon:before {
            background: url(../../assets/locationIcon.png) no-repeat left top / 1.5rem;
        }
    }
    & p {
        font-weight: 500;
        line-height: 1.9;
        & a {
            color: var(--dark-gray);
        }
    }
    @media screen and (max-width: 767px) {
        & .google-map {
            margin-bottom: 3rem;
        }
    }
}