
.termConditions{
    &_inner{
        background: var(--white-color);
        padding: 2rem 1.5rem;
        border-radius: 2rem;
        box-shadow: 0px 0px 0.4rem 0px #ddd;
    }
    & ul{
        border-right:.1rem solid var(--border-color);
        padding: 2rem 0;
        & .nav-link.active, & .show>.nav-link{
            background:none;
            color:var(--theme-color);
            border-right:.2rem solid var(--theme-color);
            border-radius:0;
        }
        & li{
            margin: 1rem 0;
            counter-increment: step-counter;
            font-weight:600;
            ::before {
                content: counter(step-counter) ".";
                margin-right: 5px;
            }
            & a{
                padding-left:0;
                &:hover{
                    color:var(--theme-color) !important;  
                    @media screen and (max-width:767px){
                        color:var(--white-color) !important; 
                    }
                }
            }
        }
        @media screen and (max-width:767px){
            border: 0;
            & .nav-link.active, & .show>.nav-link{
                background: var(--theme-color);
                border-right: 0;
                color: var(--white-color);
                border-radius: 2rem;
            }
            & .nav-link {
                border-radius: 2rem;
                padding: .4rem 1rem;
                background: var(--light-gray);
                margin-right: .5rem;
                color: var(--white-color);
                border-radius: 2rem;
                font-size: 1.2rem;
            }
        }
    }
    & .tab-content {
        margin-top: 3rem;
        overflow-y: auto;
        height: 35rem;
        @media screen and (max-width:767px){
            margin-top: 0;
        }
        & h2 {
            font-size: 1.5rem;
            font-weight: 600;
            color: var(--dark-gray);
            margin: 0 0 1rem;
        }
        & p{
            line-height: 1.6;
            font-weight: 500;
            color: var(--light-gray);
            font-size: 1.4rem;
            margin-bottom: 2rem;
        }
    } 
}
