// Main Content (Center) CSS
.center {
    width: calc(100% - var(--crl-width) - var(--crr-width));
    margin: 0 var(--crr-width) 0 var(--crl-width);
    background: var(--center-color);
    padding: 0 3.7rem;
    height: 100vh;
    display: table;
    position: absolute;
    left: 0;
    transition: all .2s;
    @media screen and (max-width: 1440px) {
        padding: 0 2rem;
    }
    @media screen and (max-width: 991px) {
        width: 100%;
        margin: 0 auto;
        padding: 0 1.5rem;
    }
    & .benifit_sec {
        background: #c9f0ff;
        border-radius: 1.8rem;
        padding: 2rem;
        position: relative;
        box-shadow: 0px 0px 0.8rem 0px #f0eded;
        margin-bottom: 3rem;
        &::after {
            content: "";
            background: url("../../assets/vector.png") no-repeat top right;
            width: 100%;
            height: 137%;
            position: absolute;
            right: 22px;
            bottom: 0;
            background-size: contain;
            @media screen and (max-width: 1400px) {
                height: 85%;
            }
            @media screen and (max-width: 1199px) {
                display: none;
            }
        }
        & .progress_bar {
            display: flex;
            // justify-content: space-between;
            width: 50%;
            position: relative;
            z-index: 1;
            margin: 3rem 0;
            @media screen and (max-width: 1199px) {
                width: 100%;
            }
            &:before {
                content: "";
                background: #fff;
                height: 0.7rem;
                border-radius: 2rem;
                width: 100%;
                position: absolute;
                top: 1.1rem;
                z-index: -1;
            }
            & .live_progress {
                background: var(--theme-color);
                height: 0.7rem;
                border-radius: 2rem;
                width: 57%;
                position: absolute;
                top: 1.1rem;
                z-index: -1;
            }
            & span{
                display: flex;
                &.bronze{
                    flex: 1;
                }
                &.gold{
                    flex: 2;
                    justify-content: flex-end;
                }
                &.silver{
                    flex: 1;
                    justify-content: flex-end;
                }
            }
            @media screen and (max-width: 1400px) {
                & p {
                    width: 50%;
                }
            }
        }
    }
    & .content_box {
        & .card {
            border: 0;
            border-radius: 1.8rem;
            padding: 2.7rem;
            height: 100%;
            & .couponCode {
                position: relative;
                & input {
                    font-weight: bold;
                    color: var(--green-color);
                    padding: 0.5rem 6.5rem 0.5rem 1.5rem;
                }
                & .copy {
                    position: absolute;
                    right: 1.5rem;
                    top: 1.3rem;
                    font-size: 1.6rem;
                }
            }
            & .share {
                margin: 2rem 0 0;
                & a {
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    font-weight: bold;
                    color: var(--dark-gray);
                    & img {
                        margin-left: 0.8rem;
                    }
                }
            }
        }
    }
}
.shineTable {
    width: 100%;
    & tr{
        & + tr{
            border-top: .1rem solid var(--border-color);
        }
        &:last-child td,
        &:last-child th {
            padding-bottom: 0;
        }
    }                
    & td,
    & th {
        padding: 2rem 0;
        color: var(--dark-gray);
        font-weight: 700;
        border-color:var(--border-color);
        font-size: 1.3rem;
    }
    & th span {
        font-weight: 300;
    }
    & td span,
    & th span {
        display: block;
    }
    & .table_vm {
        display: inline-block;
        width: 100%;
        text-align: center;
        font-size: 1.6rem;
    }
}