// Left Sidebar CSS
.left {
    width: var(--crl-width);
    background: var(--sidebar-bg-color);
    position: fixed;
    left: var(--crl-left);
    top: 0;
    height: 100vh;
    padding: 2rem 0 5rem;
    box-shadow: 0px 0px 2rem 0px #ddd;
    z-index: 100;
    // @media screen and (max-width: 991px) {
    //     display: none;
    // }
    &_sidebar {
        flex-direction: column;
        justify-content: space-between;
        display: flex;
        height: 100%;
    }
    & .logo {
        padding: 0 2rem 2rem;
    }
    &_links {
        & a {
            padding: 0.8rem 2rem;
            @include d-inline-block;
            width: 100%;
            margin-bottom: 1.5rem;
            color: var(--light-gray);
            font-weight: 600;
            &:hover {
                color: var(--dark-gray);
            }
            &.active {
                color: var(--dark-gray);
                border-right: 0.4rem solid var(--theme-color);
            }
        }
    }
    &_footer_links {
        & a {
            padding: 0.8rem 2rem;
            @include d-inline-block;
            width: 100%;
        }
    }
}
