// Right Sidebar css


.right {
    width: var(--crr-width);
    background: var(--sidebar-bg-color);
    position: fixed;
    right: var( --crr-right);
    top: 0;
    height: 100vh;
    box-shadow: 0px 0px 2rem 0px #ddd;
    padding: 2rem 0 5rem;
    overflow-y: scroll;
    z-index: 1000;
    // transition: width .2s ease;
    // @media screen and (max-width: 991px) {
    //     display: none;
    // }
    &_links {
        & .card {
            border: 0;
            background: transparent;
            & > a {
                padding: 0.8rem 2rem;
                @include d-inline-block;
                width: 100%;
                margin-bottom: 1.5rem;
                color: var(--light-gray);
                font-weight: 800;
                font-size: 1.6rem;
                border-left: 0.4rem solid transparent;
                &:hover {
                    color: var(--dark-gray);
                }
                &.active {
                    color: var(--dark-gray);
                    border-left: 0.4rem solid #4ac1f0;
                }
                &[data-toggle="collapse"][aria-expanded="true"] {
                    color: var(--dark-gray);
                    border-left: 0.4rem solid #4ac1f0;
                }        
            }
        }
        & .profile {
            position: relative;
            margin-bottom: 5rem;
            &_img {
                & img {
                    width: 14rem;
                    height: 14rem;
                    border-radius: 50%;
                    margin: 0 1rem 1rem 0;
                    border: 3px solid var(--theme-color);
                }
            }
            & .share_fb {
                position: absolute;
                top: 6rem;
            }
            & p {
                color: var(--dark-gray);
                font-weight: 500;
            }
        }
    }
    & .orderInfo {
        & .orderImg {
            width: 7.1rem;
            height: 8.8rem;
            background: #e5e5e5;
        }
        & .downArrow {
            padding: 1rem 1.5rem 0 0;
        }
        & h3 {
            color: var(--dark-gray);
            font-weight: 700;
            font-size: 1.6rem;
        }
        & .media {
            & + .media {
                border-top: 0.1rem solid var(--border-color);
                margin-top: 2rem;
                padding-top: 2rem;
            }
        }
    }
}

.logo {
    & img {
        max-width: 19rem;
    }
}

// Dashboard Header Css
.dashboard_header {
    // display: none;
    padding-top: 2rem;
    @media screen and (max-width: 991px) {
        display: block;
    }
    & .hamburger_menu {
        & span {
            display: block;
            width: 2.5rem;
            height: 0.3rem;
            position: relative;
            background: var(--theme-color);
            border-radius: 0.3rem;
            z-index: 1;
            & + span {
                margin-top: 0.5rem;
            }
        }
    }
    & .user_icon {
        & img {
            max-width: 3rem;
        }
    }
    & .logo {
        & img {
            max-width: 13rem;
        }
    }
}

.details{
    padding: 0 1rem 2rem;
    border-bottom: 1px solid #ccc;
    margin-bottom: 2rem;
    & h3{
        margin: 0 0 1rem;
        font-weight: 600;
        color: var(--dark-gray);
    }
    & p{
        font-size: 1.3rem;
    }
    &_content{
        flex: 1.1;
    }
    & .add_new{
        margin: 2rem 0;
        a {
        font-size: 1.6rem;
        font-weight: 700;
        }
    }
}
.bg {
    position: fixed;
    width: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .2);
    z-index: 2;
    display: none;
    
}