.login {
    & h1 {
        font-size: 4rem;
        margin: 5rem 0 3rem;
        font-weight: 700;
    }
    & .login_header {
        padding: 3rem 5rem 0;
        @media screen and (max-width: 767px) {
            padding: 2rem 1.5rem 0;
            text-align: center;
        }
    }
    & .loginContainer {
        padding: 6rem 9rem 3rem;
        @media screen and (max-width: 767px) {
            padding: 0rem 1.5rem 3rem;
        }
    }
    & .login_form {
        & .form-group {
            position: relative;
            margin-bottom: 1.5rem;
        }
        & .form-control {
            border: 0;
            border-bottom: 1px solid var(--border-color);
            color: var(--light-gray);
            &:focus {
                border: 0;
                border-bottom: 0.1rem solid var(--border-color);
            }
        }
        & .forgot_txt {
            width: 100%;
            text-align: right;
        }
        & .form-check-label {
            padding-left: 1rem;
            color: var(--theme-color);
            font-weight: 600;
        }
        & .chkbx_btn {
            display: flex;
            align-items: center;
            margin-top: 2rem;
            & .form-check {
                width: 50%;
            }
            & .form_btn {
                width: 50%;
                text-align: right;
                & .btn {
                    background: var(--theme-color);
                    color: var(--white-color);
                    font-weight: 600;
                    text-transform: uppercase;
                    padding: 0.6rem 4rem;
                }
            }
        }
        & .or {
            text-align: center;
            width: 10rem;
            margin: 3rem auto 2rem;
            position: relative;
            color: var(--light-gray);
            font-weight: 700;
            &:before {
                content: "";
                background: var(--border-color);
                height: 0.1rem;
                width: 3rem;
                position: absolute;
                left: 0;
                top: 1.1rem;
            }
            &:after {
                content: "";
                background: var(--border-color);
                height: 0.1rem;
                width: 3rem;
                position: absolute;
                right: 0;
                top: 1.1rem;
            }
        }
        & .loginFB {
            width: 80%;
            margin: 0 auto;
            & .fb {
                background: var(--fb-color) url(../../assets/facebook.png) no-repeat 1.5rem 1.5rem / 2rem;
                border: 0;
                padding: 1.5rem 1rem;
                width: 100%;
                color: var(--white);
            }
        }
    }
    & .right_img {
        & img {
            width: 100%;
        }
    }
}

.login_footer {
    margin-top: 3.3rem;
    & .footer_left {
        & li {
            &+li {
                margin-left: 4rem;
                @media screen and (max-width: 767px) {
                    margin-left: 2rem;
                }
            }
        }
        @media screen and (max-width: 767px) {
            & ul {
                justify-content: center;
                margin-bottom: 2rem !important;
            }
        }
    }
    & .footer_right {
        text-align: right;
        @media screen and (max-width: 767px) {
            text-align: center;
        }
        & img {
            max-width: 11rem;
            margin-left: 1.5rem;
        }
    }
}

// popup css
.popup {
    background: rgba(0, 0, 0, 0.4);
    width: 100%;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    display: flex;
    &_box {
        max-width: 116.5rem;
        width: 60%;
        padding: 3rem;
        margin: auto;
        background: var(--white-color);
        border-radius: 1.5rem;
    }
    & h1 {
        color: var(--theme-color);
        margin: 0 0 1rem;
        font-weight: bold;
        font-size: 5rem;
    }
    & h2 {
        color: var(--dark-gray);
        font-size: 3rem;
        font-weight: 600;
        margin: 0 0 1.5rem;
    }
    & p {}
    &_Btn {
        display: flex;
        margin: 4rem 0;
        & a {}
    }
}

.popup1,
.popup2,
.popup3,
.popup4 {
    display: none;
}