.form {
    padding: 0 20px;
    &-group {
        margin-bottom: 2.5rem;
        padding: 0 0.5rem;
    }
    &-control {
        height: 5rem;
        font-size: 1.4rem;
        padding: 0.5rem 1.5rem;
        border: 1px solid var(--border-color);
        &:focus {
            border: 1px solid var(--border-color);
            box-shadow: none !important;
        }
    }
    & lable {
        position: absolute;
        top: -0.9rem;
        left: 1.5rem;
        background: #fff;
        font-size: 1.3rem;
    }
    & .radio-inline {
        margin-left: 3rem;
        & input {
            margin-right: 1rem;
        }
    }
    & .form-check-label {
        padding-left: 1rem;
    }
}
form {
    & select {
        -webkit-appearance: none;
    }
    & .custom_arrow {
        width: 6rem;
        height: 5rem;
        border: 1px solid var(--border-color);
        position: absolute;
        top: 0;
        right: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }
}
